export default {
    namespaced: true,
    state:{
        players:[],
        characters:[],
        currentTeam: [],
    },
    getters:{
        GetMyData: function(state){
            return (id)=>{
                return state.players.filter(player=>player._id === id)
            }
        },  
        GetMyCharacters: function(state){
            return (id)=>{
                return state.characters.filter(character => {
                    return character.ownership === id
                })
            }
        }
    },
    mutations: {
        setGame: function(state, payload){
            state.characters = payload.characters
            state.players = payload.players
        },
        addToTeam(state, char){
            if(state.currentTeam.length >= 3){
                return
            }
            
            for(let i = 0; i < state.currentTeam.length; i++){
                if(state.currentTeam[i]._id === char._id) return
            } 
            state.currentTeam.push(char)
        }
    }
}