import {
    BackgroundRenderer,
    loadImage,
    isWebGLSupported,
    TransitionType,
    Easings,
    EffectType
} from "midori-bg";

export default {
    namespaced: true, 
    state: {
        background: '',
        midoriRenderer: null,
        canvas: null,
        isLoaded: false
    },
    mutations:{
        setHomeBackground(state){
            state.isLoaded = false
            if (isWebGLSupported) {
                state.midoriRenderer = new BackgroundRenderer(state.canvas);

                loadImage(require("@/assets/img/bg4.jpg"))
                    .then(img => {
                        state.midoriRenderer.setBackground(img, {
                            type: TransitionType.Glitch,
                            config: {
                                seed: Math.random(),
                                duration: 1.5,
                                easing: Easings.Cubic.InOut
                            }
                        });

                        const { camera } = state.midoriRenderer.background;

                        camera.sway(
                            { x: 0.1, y: 0.05, z: 0.02, zr: 1 },
                            {
                                duration: 2,
                                easing: Easings.Quadratic.InOut,
                                loop: true
                            }
                        );

                        const { effects } = state.midoriRenderer.background;
                        effects.set(EffectType.MotionBlur, { intensity: 1, samples: 32 });
                        effects.set(EffectType.RgbShift, { amount: 0.005, angle: 90 });
                        effects.set(EffectType.VignetteBlur, {
                            size: 3,
                            radius: 1.5,
                            passes: 10
                        });

                        const { particles } = state.midoriRenderer.background;

                        // generate two named groups of particles in the background.
                        particles.generate([
                            {
                                name: "small",
                                amount: 100,
                                maxSize: 5,
                                maxOpacity: 1,
                                minGradient: 0.75,
                                maxGradient: 1.0,
                                color: 0xffffff
                            },
                            {
                                name: "large",
                                amount: 30,
                                minSize: 50,
                                maxSize: 100,
                                maxOpacity: 0.1,
                                minGradient: 1.0,
                                maxGradient: 1.0,
                                color: 0xffffff
                            }
                        ]);
                        particles.sway(
                            "large",
                            { x: 0.025, y: 0.025 },
                            { duration: 1.5, easing: Easings.Sinusoidal.InOut, loop: true }
                        );
                        particles.sway(
                            "small",
                            { x: 0.025, y: 0.025 },
                            { duration: 1.5, easing: Easings.Sinusoidal.InOut, loop: true }
                        );

                        setTimeout(()=>{
                            state.isLoaded = true;
                        }, 1250);
                    })
                    .catch(err => {
                        alert(err);
                    });
            }
        },
        setCanvas(state, element){
            state.canvas = element
        },
        setBackgroundImage(state, bgURL){
            state.background = bgURL
        }
    }
}