<template>
  <main id='app'>
    <canvas class='canvas' ref='canvas'></canvas>

    <transition name='fade'>
      <router-view v-show='isLoaded' />
    </transition>
  </main>
</template>

<script>
import axios from "axios";
export default {
  computed: {
    isLoaded() {
      return this.$store.state.midori.isLoaded;
    }
  },
  created() {
    axios
      .get("/character")
      .then(res => {
        this.$store.commit("Roster/setData", res.data);
      })
      .catch(err => {
        console.log(err);
      });
    this.$store.commit("user/SET_USER", {
      _id: 1,
      userName: Math.random()
        .toString(36)
        .replace(/[^a-z]+/g, "")
        .substr(0, 9),
      isLoggedIn: true,
      avatarId: Math.random()
        .toString(36)
        .replace(/[^a-z]+/g, "")
        .substr(0, 5)
    });
  },
  mounted() {
    this.$store.commit("midori/setCanvas", this.$refs.canvas);
  }
};
</script>

<style lang="scss">
@import "./style.scss";
</style>
