import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: "/",
    component: ()=> import('../views/HomeScreen'),
    children: [
      {
        path: '',
        name: 'home',
        component: ()=>import('../views/Selection')
      },
      {
        path: 'lobby',
        name: 'lobby',
        component: ()=>import("../views/Lobby")
      }
    ]
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
